@charset "UTF-8";
.grid {
  columns: 1;
  column-gap: 3rem;
}

@media (min-width: 38em) {
  .grid {
    columns: 2;
  }
}

.grid > * {
  display: inline-block;
  margin-bottom: 3rem;
}

.section h2 {
  position: relative;
}

.section h2 a {
  display: block;
  margin-bottom: 4.5rem;
}

.section h2 a:before {
  position: absolute;
  font-size: .75rem;
  border-top: 4px solid #f0c674;
  padding-top: .45rem;
  bottom: -1.75rem;
  font-weight: 400;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  content: " ";
  width: 2rem;
}

#developers {
  color: #c5c9c6;
}

#developers h2,
#developers h3 {
  color: #fff;
}

#developers h2 {
  font-weight: 600;
}

#developers h3 {
  font-weight: 400;
}
